.home-page {
  flex-direction: column;
  gap: 2rem;
  padding: 2rem 1.5rem 100px;
  display: flex;
}

.section-title {
  color: var(--text-color);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.streak-tracker {
  width: 100%;
}

.streak-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.pass-container {
  align-items: center;
  gap: .5rem;
  display: flex;
}

#use-pass-btn {
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  padding: .3rem .6rem;
  font-size: .8rem;
}

#pass-count {
  font-weight: 700;
}

.streak-circles {
  justify-content: space-between;
  gap: .5rem;
  display: flex;
}

.streak-circle {
  aspect-ratio: 1;
  background-color: var(--card-background);
  border: 3px solid var(--border-color);
  cursor: pointer;
  border-radius: 50%;
  flex: 1;
  transition: all .4s;
}

.streak-circle.completed {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  transform: scale(1.1);
  box-shadow: 0 4px 15px #4caf5066;
}

.daily-task-card {
  background: var(--card-background);
  text-align: center;
  box-shadow: 0 10px 30px var(--shadow-light);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
}

.task-card-content h3 {
  color: var(--light-text-color);
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: 600;
}

.task-card-content p {
  color: var(--text-color);
  justify-content: center;
  align-items: center;
  min-height: 80px;
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
}

.task-buttons {
  gap: 1rem;
  display: flex;
}

#prove-task-btn, #refresh-task-btn {
  cursor: pointer;
  border: none;
  border-radius: 15px;
  flex: 1;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  transition: all .3s;
}

#prove-task-btn {
  background: linear-gradient(45deg, var(--secondary-color), #ffbf00);
  color: var(--white-color);
  box-shadow: 0 4px 20px #ff98004d;
}

#prove-task-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 7px 25px #ff980066;
}

#refresh-task-btn {
  color: var(--light-text-color);
  background-color: #f0f2f5;
}

#prove-task-btn i, #refresh-task-btn i {
  margin-right: .5rem;
}

.bottom-nav {
  background-color: var(--card-background);
  width: 100%;
  height: 80px;
  box-shadow: 0 -5px 20px var(--shadow-light);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.nav-item {
  color: var(--light-text-color);
  flex-direction: column;
  align-items: center;
  font-size: .8rem;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav-item i {
  margin-bottom: .25rem;
  font-size: 1.5rem;
}

.nav-item.active {
  color: var(--primary-color);
  transform: translateY(-5px);
}

.animate-fade-in {
  animation: .8s ease-out forwards fadeIn;
}

.animate-slide-up {
  animation: .8s ease-out forwards slideUp;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.app-header-desktop, .app-footer-desktop {
  display: none;
}

@media (width >= 769px) {
  .bottom-nav {
    display: none;
  }

  .app-header-desktop {
    background-color: var(--card-background);
    width: 100%;
    box-shadow: 0 2px 10px var(--shadow-light);
    z-index: 100;
    padding: 1rem 2rem;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header-content {
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
  }

  .logo-container {
    align-items: center;
    display: flex;
  }

  .header-logo {
    width: 40px;
    height: 40px;
    margin-right: 1rem;
  }

  .settings-menu {
    position: relative;
  }

  .settings-btn {
    cursor: pointer;
    color: var(--text-color);
    background: none;
    border: none;
    font-size: 1.5rem;
  }

  .dropdown-menu {
    background-color: var(--card-background);
    box-shadow: 0 5px 15px var(--shadow-light);
    z-index: 101;
    border-radius: 10px;
    display: none;
    position: absolute;
    top: 120%;
    right: 0;
    overflow: hidden;
  }

  .dropdown-menu.show {
    display: block;
  }

  .dropdown-menu a {
    color: var(--text-color);
    white-space: nowrap;
    padding: .75rem 1.5rem;
    text-decoration: none;
    display: block;
  }

  .dropdown-menu a:hover {
    background-color: #f0f2f5;
  }

  .app-footer-desktop {
    width: 100%;
    color: var(--white-color);
    background-color: #333;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 2rem;
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  .footer-links a {
    color: var(--white-color);
    margin-right: 1.5rem;
    text-decoration: none;
  }

  .social-media-links a {
    color: var(--white-color);
    margin-left: 1.5rem;
    font-size: 1.5rem;
  }
}

@media (width <= 768px) {
  .home-page {
    padding: 1.5rem 1rem 100px;
  }

  .section-title {
    font-size: 1.1rem;
  }

  .streak-circle {
    width: auto;
  }

  .daily-task-card {
    padding: 1.5rem;
  }

  .task-card-content p {
    font-size: 1.3rem;
  }
}
