/* --- YENİ ANA SAYFA STİLLERİ --- */
.home-page {
    padding: 2rem 1.5rem 100px 1.5rem; /* Alttaki nav bar için boşluk */
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.section-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

/* Haftalık Seri Takibi */
.streak-tracker {
    width: 100%;
}

.streak-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.pass-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

#use-pass-btn {
    background: none;
    border: 1px solid var(--secondary-color);
    color: var(--secondary-color);
    padding: 0.3rem 0.6rem;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.8rem;
}

#pass-count {
    font-weight: 700;
}

.streak-circles {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.streak-circle {
    flex: 1;
    aspect-ratio: 1 / 1; /* Daire formunu koru */
    border-radius: 50%;
    background-color: var(--card-background);
    border: 3px solid var(--border-color);
    transition: all 0.4s ease;
    cursor: pointer;
}

.streak-circle.completed {
    background-color: var(--primary-color);
    border-color: var(--primary-color);
    transform: scale(1.1);
    box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

/* Günün İyilik Görevi Kartı */
.daily-task-card {
    background: var(--card-background);
    border-radius: 20px;
    padding: 2rem;
    text-align: center;
    box-shadow: 0 10px 30px var(--shadow-light);
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

.task-card-content h3 {
    font-size: 1rem;
    font-weight: 600;
    color: var(--light-text-color);
    margin-bottom: 1rem;
}

.task-card-content p {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--text-color);
    min-height: 80px; /* Metin değiştiğinde zıplamayı önler */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.task-buttons {
    display: flex;
    gap: 1rem;
}

#prove-task-btn, #refresh-task-btn {
    flex: 1;
    padding: 1rem;
    border: none;
    border-radius: 15px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
}

#prove-task-btn {
    background: linear-gradient(45deg, var(--secondary-color), #ffbf00);
    color: var(--white-color);
    box-shadow: 0 4px 20px rgba(255, 152, 0, 0.3);
}

#prove-task-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 25px rgba(255, 152, 0, 0.4);
}

#refresh-task-btn {
    background-color: #f0f2f5;
    color: var(--light-text-color);
}

#prove-task-btn i, #refresh-task-btn i {
    margin-right: 0.5rem;
}

/* Alt Navigasyon Barı (Mobil) */
.bottom-nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: var(--card-background);
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 -5px 20px var(--shadow-light);
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
}

.nav-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: var(--light-text-color);
    font-size: 0.8rem;
    transition: all 0.3s ease;
}

.nav-item i {
    font-size: 1.5rem;
    margin-bottom: 0.25rem;
}

.nav-item.active {
    color: var(--primary-color);
    transform: translateY(-5px);
}

/* Animasyonlar */
.animate-fade-in {
    animation: fadeIn 0.8s ease-out forwards;
}

.animate-slide-up {
    animation: slideUp 0.8s ease-out forwards;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideUp {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

/* Web için Header, Footer ve Ayarlar Menüsü */
.app-header-desktop {
    display: none; /* Mobilde gizli */
}
.app-footer-desktop {
    display: none; /* Mobilde gizli */
}

@media (min-width: 769px) {
    .bottom-nav {
        display: none; /* Web'de gizli */
    }
    .app-header-desktop {
        display: block;
        width: 100%;
        background-color: var(--card-background);
        box-shadow: 0 2px 10px var(--shadow-light);
        padding: 1rem 2rem;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
    }
    .header-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1200px;
        margin: 0 auto;
    }
    .logo-container {
        display: flex;
        align-items: center;
    }
    .header-logo {
        width: 40px;
        height: 40px;
        margin-right: 1rem;
    }
    .settings-menu {
        position: relative;
    }
    .settings-btn {
        background: none;
        border: none;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--text-color);
    }
    .dropdown-menu {
        display: none;
        position: absolute;
        right: 0;
        top: 120%;
        background-color: var(--card-background);
        border-radius: 10px;
        box-shadow: 0 5px 15px var(--shadow-light);
        overflow: hidden;
        z-index: 101;
    }
    .dropdown-menu.show {
        display: block;
    }
    .dropdown-menu a {
        display: block;
        padding: 0.75rem 1.5rem;
        text-decoration: none;
        color: var(--text-color);
        white-space: nowrap;
    }
    .dropdown-menu a:hover {
        background-color: #f0f2f5;
    }
    .app-footer-desktop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 1.5rem 2rem;
        background-color: #333;
        color: var(--white-color);
        position: fixed;
        bottom: 0;
        left: 0;
    }
    .footer-links a {
        color: var(--white-color);
        text-decoration: none;
        margin-right: 1.5rem;
    }
    .social-media-links a {
        color: var(--white-color);
        font-size: 1.5rem;
        margin-left: 1.5rem;
    }
}

/* HOME PAGE MOBILE STYLES */
@media (max-width: 768px) {
    .home-page {
        padding: 1.5rem 1rem 100px 1rem;
    }
    .section-title {
        font-size: 1.1rem;
    }
    .streak-circle {
        width: auto;
    }
    .daily-task-card {
        padding: 1.5rem;
    }
    .task-card-content p {
        font-size: 1.3rem;
    }
}